import axios from 'axios'
import MockAdapter from 'axios-mock-adapter'
import objects from './objects.json'
import summary from './summary.json'
import scenarios from './scenarios.json'
import interactQueries from './interactQueries.json'
import messages from './messages.json'
import moment from 'moment'
var mock = new MockAdapter(axios)

mock.onPost('/jeedom/core/api/jeeApi.php').reply(function (config) {
  const requestData = JSON.parse(config.data)
  let result = null
  switch (requestData.method) {
    case 'event::changes': {
      const datetime = Date.now() / 1000
      const temp204 = (Math.round(Math.random() * 5) / 10 + 24.1).toFixed(1)
      const temp207 = (Math.round(Math.random() * 5) / 10 + 20.5).toFixed(1)
      result = {
        datetime,
        result: [
          { datetime: datetime, name: 'cmd::update', option: { cmd_id: '204', value: temp204, display_value: temp204, valueDate: datetime, collectDate: moment().toISOString(), alertLevel: 'none' } },
          { datetime: datetime, name: 'cmd::update', option: { cmd_id: '207', value: temp207, display_value: temp207, valueDate: datetime, collectDate: moment().toISOString(), alertLevel: 'none' } },
        ],
      }
      break
    }
    case 'object::full':
      objects.forEach((object) => {
        object.eqLogics.forEach((eqLogic) => {
          eqLogic.status.lastCommunication = new Date()
        })
      })
      result = objects
      break
    case 'user::getHash':
      result = 'fakehash'
      break
    case 'summary::global':
      result = summary
      break
    case 'summary::byId': {
      switch (requestData.params.key) {
        case 'light':
          result = Math.floor(Math.random() * 2)
          break
        case 'temperature':
          result = Math.floor(Math.random() * 20) + 10
          break
        case 'humidity':
          result = Math.floor(Math.random() * 50) + 25
          break
        case 'power':
          result = Math.floor(Math.random() * 3000)
          break
        default:
          result = 0
          break
      }
      break
    }
    case 'scenario::all':
      result = scenarios
      break
    case 'scenario::changeState':
      result = true
      break
    case 'cmd::getHistory':
      result = []
      for (let index = 0; index < 48; index++) {
        result.push({
          value: Math.floor(Math.random() * 15) + 10,
          datetime: moment().subtract(index * 30, 'minutes'),
        })
      }
      break
    case 'cmd::getStatistique': {
      switch (requestData.params.id) {
        case 205:
        case 263:
        case 211:
          result = { min: 500, avg: 650, max: 950 }
          break
        case 206:
        case 261:
        case 208:
        case 212:
          result = { min: 45, avg: 50, max: 60 }
          break
        case 210:
        case 204:
        case 207:
        case 260:
          result = { min: 15, avg: 22, max: 25 }
          break
        case 214:
          result = { min: 0, avg: 0.5, max: 2 }
          break
        case 387:
        case 389:
          result = { min: 0, avg: 3.6, max: 10 }
          break
        case 388:
        case 390:
          result = { min: 0, avg: 0.5, max: 2 }
          break
        default:
          result = { min: 10, avg: 15, max: 20 }
          break
      }
      break
    }
    case 'cmd::execCmd':
      result = []
      break
    case 'interactQuery::all':
      result = interactQueries
      break
    case 'interact::tryToReply':
      result = interactQueries.filter((reply) => reply.query === requestData.params.query)
      if (result.length > 0) {
        result = { reply: result[0].reply }
      } else {
        result = { reply: 'D\u00e9sol\u00e9 je n\'ai pas compris la demande' }
      }
      break
    case 'message::all':
      result = messages.map((message) => {
        message.date = new Date()
        return message
      })
      break
    default:
      console.error('API method not handled', config)
  }
  return [
    200,
    { jsonrpc: '2.0', id: 1587917123770, result },
  ]
})

mock.onGet().passThrough()
